.popup__message {
  color: #000000;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 900;
  max-width: 358px;
  text-align: center;
  margin: 0 auto 60px;
}

@media screen and (max-width: 555px) {
  .popup__message {
    font-size: 18px;
    align-self: auto;
    margin: 0 0 75px;
  }
}